/* Big tablet to 1200px (widths smaller taht the 1140px row) */
@media only screen and (max-width: 1300px) {
    html {
        font-size: 53%;
    }
}

/* Small tablet to big tablet: from 768px to 1023px */
@media only screen and (max-width: 1023px) {
    html {
        font-size: 53%;
    }

    .Content_container {
        display: flex;
        justify-content: center;

        .Content_container {
            .Content {
                display: flex;
                justify-content: center;
    
                .pic {
                    width: 100%;
                    margin: 0 0.8rem;
    
                    img {
                        width: 100%;
                        border-radius: 10px;
                    }
                }
    
                .text {
                    width: 100%;
                    padding: 1rem;
                    margin: auto;
                    background-color: rgba(255, 255, 255, 0.1);
                    border-radius: 3px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                }
            }
        }
    }
}

/* Small phones to small tablets: from 481px to 767px */
@media only screen and (max-width: 767px) {
    * {
        cursor: default;
    }
    html {
        font-size: 45%;
    }
    .Shelf {
        cursor: default;
    }

    .info {
        bottom: 4rem;
        right: 0.5rem;
        font-size: 1.6rem;
    }
    .Content_container {
        .Content {
            display: flex;
            justify-content: center;

            .pic {
                width: 100%;
                margin: 0 0.8rem;

                img {
                    width: 100%;
                    border-radius: 10px;
                }
            }

            .text {
                width: 100%;
                padding: 1rem;
                margin: auto;
                background-color: rgba(255, 255, 255, 0.1);
                border-radius: 3px;
                display: flex;
                align-items: center;
                justify-content: center;
            }
        }
    }
}

/* Small phones: from 0 to 480px */
@media only screen and (max-width: 480px) {
    * {
        cursor: default;
    }
    .ShelfList {
        width: 65%;
        padding: 10px;
    }

    .info {
        bottom: 4rem;
        right: 0.5rem;
        text-align: start;
        font-size: 1.3rem;
    }
    .Content_container {
        .Content {
            display: flex;
            justify-content: center;

            .pic {
                width: 100%;
                margin: 0 0.8rem;

                img {
                    width: 100%;
                    border-radius: 10px;
                }
            }

            .text {
                width: 100%;
                padding: 1rem;
                margin: auto;
                background-color: rgba(255, 255, 255, 0.1);
                border-radius: 3px;
                display: flex;
                align-items: center;
                justify-content: center;
            }
        }
    }
}
